.popup__title {
    margin: 0 0 14px;
    padding: 0;
    font-family: 'Inter', Arial, sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
}

@media screen and (max-width: 525px) {
    .popup__title {
        font-size: 18px;
        line-height: 22px;
    }
}